<template>
<section>
    <div>
        <loader :is-visible="store.getLoadingStatus"></loader>
        <tg-list :RenderRowActionMethod="renderActions" :IdentityColumn="trackId" ModuleName="Timer" :listType="[DetailListType]" HeaderText="Flag Track Entry" SubModuleCode="Timer" :SortExp="SortExp" :callbackfunction="FetchData" :LegendArray="[]" :SortBy="SortBy" :IsShowAction="false" :ListData="store.getTrackEntryFlag" :HeaderData="Headers" :TotalRecords="TotalRecords" :showCheckBox="true" :ListDataCallBackFunction="FetchData" :NorecordfoundText="$t('NoRecordfound')" :HeaderButtons="entryButton" :widgets="[]" v-on:HeaderButtonClick="commonHeaderButtonClick" v-on:PagerButtonClick="pagerMethod" v-on:SortdataButtonClick="sortdata" v-on:LeftsearchButtonClick="leftCommonSearch" :SearchFields="leftSearchFields">
            <template v-slot:slotdata="{ data }">
                <template v-if="data.column.COLUMN_NAME == 'comment'">

                    <em aria-hidden="true" class="fa fa-comment-o  text-primary" @click="commentModalShow(data.row,true)"></em>
                    <span class="noti-circle noti-blue text-center">{{ data.row.commentsCount }}</span>
                </template>
            </template>
        </tg-list>
    </div>
    <Comments v-if="commentShow" v-bind:store="store" @close="commentShow=false" v-bind:flagData="flagData"/>
</section>
</template>

<script>
import {
    defineComponent,
} from '@vue/composition-api'
import Entry from './EntryFlag'
import Comments from "@/components/EntryFlags/FlagUnFlag.vue"
import {
    storeToRefs
} from "pinia";
import {
    useloggedUserInfo
} from "@/stores/UserInfo";
const {
    GetUserInfo
} = storeToRefs(useloggedUserInfo());
export default defineComponent({
    components: {
        Comments
    },
    props: {
        store: {
            type: Object
        },

    },
    created: async function () {
        Entry.bindleftCommonSearchdropdown(this, GetUserInfo.value.UserType);
        //     let url = `projectId=0&datefrom=&dateto=&pageSize=10&pageNum=1&sortBy=&sortExp=`
        //    await this.store.trackEntryFlag(url)
        this.FetchData()

    },
    methods: {
        async FetchData() {
            var vueObj = this;
            var url = `pageSize=${vueObj.pageSize}&pageNum=${vueObj.pageNum}&sortBy=${vueObj.sortBy}&sortExp=${vueObj.sortExp}`;
            await this.store.trackEntryFlag(url)
            vueObj.TotalRecords = this.store.getTrackEntryFlag[0].total
            vueObj.trackId = this.store.getTrackEntryFlag[0].trackId
            let checkBox = false
            vueObj.store.getTrackEntryFlag.map((item) => {

                if (item.statusCode.toUpperCase() == "TRACK_ENTRY_APPROVED" || item.statusCode.toUpperCase() == "TRACK_ENTRY_REJECTED" || item.statusCode == "TRACK_ENTRY_DELETE_PENDING" || item.approversCount != 0 || item.projectStatus == "INACT" || item.projectStatus == "DEL" || item.createdByName == GetUserInfo.value.Name) {
                    item.isCheckBoxDisabled = true
                } else {
                    checkBox = true
                    item.isCheckBoxDisabled = false
                }
                item.additionalClass = vueObj.addClass(item.statusCode)
                //console.log("checaddClasskkkk", vueObj.addClass(item.statusCode))
                return item
            })
            if(checkBox == false){
                let checkBoxNode = document.querySelector(".chkItems")
                checkBoxNode.disabled = true
            }
          //  console.log("check", vueObj.store.getTrackEntryFlag[0])

        },
        commentModalShow(data, show) {
            this.flagData = data
            this.commentShow = show
            console.log("test", data, show)

        },
        renderActions: function (row) {
            var self = this;
            let rtnActionArray = [];
            self.listActions.forEach(actionItem => {
                let action = {
                    ...actionItem
                };
                switch (action.title) {
                    case 'Edit':
                        action.isVisible = true;
                        break;
                    case 'Delete':
                        action.isVisible = true;
                        break;
                    default:
                        action.isVisible = true;
                        break;
                }
                rtnActionArray.push(action);
            });
            return rtnActionArray;
        },
        commonHeaderButtonClick(event) {
            switch (event.callbackfunction) {
                case "Flag":
                   // this.openModal
                    break;
                case "UnFlag":
                    //this.openModal()
                    break;

            }
        },
        openModal(){
            this.commentShow = true
        },
        pagerMethod: function (value) {
            this.pageSize = value.PageSize;
            this.pageNum = value.PageNumber;
            this.FetchData();
        },
        leftCommonSearch: function (value) {
            this.PageNumber = value.PageNumber;
            this.isPaged = 1;
            if (value.searchConditionJson.includes('projectId')) {
                this.projectId = value.searchConditionJson;
            } else {
                this.Userid = value.searchConditionJson;
            }
            this.modelinputdate = null;
            this.FetchData();
        },
        sortdata: function (s) {
            var SortDir = "sort tb_headerSortDown";
            this.sortBy = s;
            if (s === this.sortBy) {
                if (this.sortExp == "") {
                    this.sortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                } else if (this.sortExp == "ASC") {
                    this.sortExp = "DESC";
                    SortDir = "sort tb_headerSortUp";
                } else {
                    this.sortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                }
            }
            this.FetchData();
            $("#" + s + "  span:first-child").attr("class", SortDir);
        },
        addClass(statusCode) {

            var entryType = statusCode.toUpperCase();
            var entryStatusClass = "";
            switch (entryType) {

                case "TRACK_ENTRY_REJECTED":
                    entryStatusClass = "delpending";

                    break;
                case "TRACK_ENTRY_UPDATE_PENDING":
                    entryStatusClass = "high-profile-bar";

                    break;
                case "TRACK_ENTRY_DELETE_PENDING":
                    entryStatusClass = "high-profile-bar";

                    break;
                case "TRACK_ENTRY_ADD_MANUAL_PENDING":
                    entryStatusClass = "high-profile-bar";

                    break;
                case "TRACK_ENTRY_APPROVED":
                    entryStatusClass = "lgtr-approved";
                    break;
                case "SCREEN_FLAGED":
                    entryStatusClass = "critical-bar";
                    break;
                default:
                    entryStatusClass = "";
                    //  tip = "";
                    break;
            }
            return entryStatusClass

        }

    },
    data() {
        return {
            Headers: Entry.Headers,
            SortBy: '',
            SortExp: "",
            pageSize: 20,
            pageNum: 1,
            TotalRecords: 0,
            TotalPages: 1,
            leftSearchFields: Entry.leftsearchSchema,
            entryButton: Entry.buttons,
            projectId: '',
            Userid: '',
            trackId: 0,
            showCommentModal: false,
            commentShow: false,
            DetailListType: 'List',
            flagData:''

        }
    },
})
</script>
